@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css");@import url('https://fonts.googleapis.com/css2?family=Russo+One&display=swap');

body { background-color: #0B0B0B; margin: 0; padding: 0; font-family: 'Pretendard', sans-serif; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; display: flex; justify-content: center; color: white; } 
body * { -webkit-tap-highlight-color: transparent; } 

p { margin: 0; white-space: pre-line; } 

.loading-container { display: flex; align-items: center; }

.main { width: 1440px; display: flex; align-items: center; flex-direction: column; } 

.fade-enter, .fade-appear { opacity: 0; }
.fade-enter-active, .fade-appear-active { opacity: 1; transition: opacity 300ms; }
.fade-exit { opacity: 1; }
.fade-exit-active { opacity: 0; transition: opacity 300ms; }