/* 홈 최상단 헤더 */
.header-cover { width: calc(100% - 166px); height: 60px; background: linear-gradient(90deg, #616060 100%, #D9D9D900 0%); margin-top: 48px; display: flex; padding: 0.5px; border-radius: 30px; box-shadow: 0px 8px 4px 0px rgba(0, 0, 0, 0.25); } 
.header { position: relative; z-index: 100; width: 100%; height: 24px; display: flex; align-items: center; justify-content: space-between; background-color: #0E0822; border-radius: 30px; padding: 18px 36px 18px 48px; } 
.header .brand { font-family: 'Russo One'; font-size: 20px; line-height: 24px; cursor: default; } 
.header .center { display: flex; align-items: center; } 
.header .center p { position: relative; cursor: pointer; margin-right: 60px; } 
.header .center p.active::after { position: absolute; content: ''; width: 51px; height: 4px; border-radius: 50px; background-color: #AB41FF; bottom: -6px; left: -4.5px; } 
.header .center p.active-long::after { position: absolute; content: ''; width: 70px; height: 4px; border-radius: 50px; background-color: #AB41FF; bottom: -6px; left: -4.5px; } 
.header .center p:hover { color: #B7B7B7; } 
.header .right { position: relative; color: #B14BFF; font-weight: 700; cursor: pointer; } 
.header .right.active::after { position: absolute; content: ''; width: 51px; height: 4px; border-radius: 50px; background-color: #AB41FF; bottom: -6px; left: -4.5px; } 
.header .right:hover { color: #9B17FF; } 

/* 홈 */
.home { position: relative; width: calc(100% - 166px); min-height: 700px; max-height: 1200px; display: flex; align-items: center; } 
.home .container { position: relative; width: 100%; height: 700px; display: flex; align-items: center; } 
.home .container .left { width: 500px; height: 100%; display: flex; flex-direction: column; justify-content: center; } 
.home .container .left .title { font-size: 24px; color: #C8C8C8; line-height: 24px; margin-bottom: 20px; } 
.home .container .left .name { font-size: 76px; font-family: 'Russo One'; line-height: 76px; margin-bottom: 52px; } 
.home .container .left .description { font-size: 18px; line-height: 24px; } 
.home .container .left .description span { font-weight: 700; } 
.home .container .right { position: relative; display: flex; align-items: center; top: 0; right: -10px; } 
.home .container .word { position: absolute; font-size: 32px; font-family: 'Russo One'; opacity: 0.2; z-index: 200; } 
.home .container .right .background { position: absolute; z-index: 200; left: 80px; top: -250px; } 
.home .container .right .yoonsung { position: absolute; z-index: 300; left: 200px; top: -314px; } 
.home .container .right .cv { position: absolute; z-index: 400; width: 180px; height: 180px; background: linear-gradient(180deg, #333 0%, rgba(51, 51, 51, 0.00) 100%); display: flex; align-items: center; justify-content: center; flex-direction: column; left: 343px; top: 0px; border-radius: 50%; } 
.home .container .right .cv p { font-size: 40px; line-height: 48px; margin-top: 5px; font-family: 'Russo One'; } 
.home .container .right .cv .bottom-line { display: flex; align-items: center; margin-top: 15px; } 
.home .container .right .cv .bottom-line .cv-button:first-child { width: 73px; height: 71px; background-image: url(../src/asset/image/cv-button.png); background-repeat: no-repeat; background-size: contain; cursor: pointer; } 
.home .container .right .cv .bottom-line .cv-button:last-child { width: 73px; height: 71px; background-image: url(../src/asset/image/cv-button-eng.png); background-repeat: no-repeat; background-size: contain; cursor: pointer; } 
.home .container .right .cv .bottom-line .cv-button:first-child:hover { background-image: url(../src/asset/image/cv-button-hover.png); background-repeat: no-repeat; background-size: contain; cursor: pointer; } 
.home .container .right .cv .bottom-line .cv-button:last-child:hover { background-image: url(../src/asset/image/cv-button-eng-hover.png); background-repeat: no-repeat; background-size: contain; cursor: pointer; } 
.home .container .right .cv .bottom-line .cv-button:first-child { margin-right: 10px; } 
.home .container .right .cv .bottom-line p { font-size: 18px; font-family: 'Pretendard'; } 
.home .back { position: absolute; right: -29px; width: 537px; height: 100%; background: linear-gradient(180deg, #180C96 0%, #4A0B9D 21.35%, #A90EAB 48.96%, #BE7CAA 68.75%, #B475AC 83.33%, #885BB1 100%); filter: blur(100px); } 

/* 홈 About */
.about { position: relative; z-index: 100; width: 100vw; background-color: #15081F; display: flex; align-items: center; flex-direction: column; } 
.about .about-header { margin-top: 68px; font-size: 48px; font-family: 'Russo One'; line-height: 48px; text-align: center; } 
.about .container { width: 1274px; padding: 80px 132px 68px 132px; display: flex; justify-content: center; } 
.about .container .left { display: flex; flex-direction: column; margin-right: 162px; } 
.about .container .left .profile .title { font-size: 32px; font-family: 'Russo One'; line-height: 40px; margin-bottom: 32px; } 
.about .container .left .profile .each-line { display: flex; align-items: center; margin-bottom: 24px; } 
.about .container .left .profile .each-line p { margin-left: 16px; font-size: 24px; } 
.about .container .left .profile .each-line a { color: white; text-decoration: none; cursor: pointer; } 
.about .container .left .profile .each-line a:hover { color: #B14BFF; font-weight: 700; text-decoration: solid; cursor: pointer; } 
.about .container .left .work { margin-top: 60px; } 
.about .container .left .work .title { font-size: 32px; line-height: 40px; margin-bottom: 32px; font-family: 'Russo One'; } 
.about .container .left .work .company-line { display: flex; align-items: center; margin-bottom: 24px; } 
.about .container .left .work .company-line .date { font-size: 16px; line-height: 24px; font-weight: 700; color: #B14BFF; margin-right: 16px; } 
.about .container .left .work .company-line .name { font-size: 24px; line-height: 24px; font-weight: 700; margin-right: 8px; } 
.about .container .left .work .company-line a { display: flex; align-items: center; } 
.about .container .left .work .company-line img { cursor: pointer; } 
.about .container .left .work .description p { line-height: 20px; margin-bottom: 10px; } 
.about .container .left .work .description span { font-weight: 700; } 
.about .container .right { width: 526px; } 
.about .container .right .education .title { font-size: 32px; line-height: 40px; margin-bottom: 32px; font-family: 'Russo One'; } 
.about .container .right .education .education-container { position: relative; border-left: 0.5px solid white; padding-left: 34px; } 
.about .container .right .education .education-container .dot { position: absolute; width: 20px; height: 20px; border-radius: 50%; background-color: #B14BFF; left: -10px; } 
.about .container .right .education .education-container .each-block { margin-bottom: 68px; } 
.about .container .right .education .education-container .each-block:last-child { margin-bottom: 12px; } 
.about .container .right .education .education-container .each-block .date { line-height: 16px; margin-bottom: 16px; } 
.about .container .right .education .education-container .each-block .title { line-height: 24px; margin-bottom: 20px; font-size: 24px; font-family: 'Pretendard'; font-weight: 700; } 
.about .container .right .education .education-container .each-block .description-group .each-line { display: flex; align-items: center; margin-bottom: 10px; } 
.about .container .right .education .education-container .each-block .description-group .each-line a { display: flex; align-items: center; } 
.about .container .right .education .education-container .each-block .description-group .each-line a img { margin-left: 8px; cursor: pointer; } 

/* 홈 Skill */
.skill { width: 100vw; padding: 68px 0 84px; display: flex; align-items: center; flex-direction: column; } 
.skill .title { line-height: 48px; font-size: 48px; font-family: 'Russo One'; } 
.skill .skill-container { position: relative; width: 1274px; margin-top: 80px; } 
.skill .skill-container .left-back { position: absolute; z-index: 0; top: 240px; left: -240px; } 
.skill .skill-container .right-back { position: absolute; z-index: 0; top: -60px; right: -200px; } 
.skill .skill-container .each-block { position: relative; border-top: 0.5px solid #474646; padding-top: 32px; margin-bottom: 68px; } 
.skill .skill-container .each-block:last-child { margin-bottom: 0px; } 
.skill .skill-container .each-block .small-title { position: absolute; top: -14px; left: 50%; transform: translateX(-50%); width: 100px; height: 27px; background-color: #212121; border-radius: 50px; display: flex; justify-content: center; align-items: center; } 
.skill .skill-container .each-block .small-title p { font-weight: 700; line-height: 27px; } 
.skill .skill-container .each-block .skill-group { display: flex; align-items: center; justify-content: space-between; } 
.skill .skill-container .each-block .skill-group .each-skill { width: 150px; height: 150px; border-radius: 30px; display: flex; align-items: center; justify-content: center; flex-direction: column; background-color: rgba(41, 41, 41, 0.80); box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25); cursor: default; } 
.skill .skill-container .each-block .skill-group .each-skill:hover { background: rgba(92, 0, 161, 0.80); box-shadow: 0px 16px 16px 0px rgba(0, 0, 0, 0.25) inset; } 
.skill .skill-container .each-block .skill-group .each-skill p { margin-top: 16px; line-height: 24px; font-size: 20px; font-weight: 700; } 

/* 홈 Project */
.project { width: 100vw; padding: 68px 0 100px; display: flex; align-items: center; flex-direction: column; background-color: #15081F; } 
.project .title { font-size: 48px; line-height: 48px; font-family: 'Russo One'; } 
.project .project-container { width: 1274px; margin-top: 80px; } 
.project .project-container .top-line { position: relative; width: 100%; height: 0.5px; background-color: #474646; margin-bottom: 62px; } 
.project .project-container .top-line p { position: absolute; top: -14px; left: 50%; transform: translateX(-50%); width: 150px; height: 27px; background-color: #212121; display: flex; align-items: center; justify-content: center; border-radius: 50px; font-weight: 700; } 
.project .project-container .each-block { width: 100%; border: 2px solid #D9D9D9; display: flex; align-items: center; margin-bottom: 100px; border-radius: 10px; box-shadow: 0 8px 8px 0 rgba(255, 255, 255, 0.25); } 
.project .project-container .each-block:last-child { margin-bottom: 0px; } 
.project .project-container .each-block .left { width: calc(100% - 437px); border-right: 0.5px solid white; } 
.project .project-container .each-block .left .title-group { display: flex; align-items: center; justify-content: space-between; padding: 16px 24px; border-bottom: 0.5px solid white; } 
.project .project-container .each-block .left .title-group p { font-size: 20px; line-height: 32px; font-weight: 700; } 
.project .project-container .each-block .left .title-group .button { width: 100px; height: 30px; display: flex; align-items: center; justify-content: center; font-weight: 700; background: linear-gradient(180deg, #AB5BE8 0%, rgba(133, 14, 224, 0.50) 100%); border-radius: 4px; cursor: pointer; font-size: 14px; } 
.project .project-container .each-block .left .title-group .button:hover { background: linear-gradient(180deg, #8905EE 0%, rgba(97, 6, 167, 0.50) 100%); } 
.project .project-container .each-block .left .description-group { padding: 20px 24px; border-bottom: 0.5px solid white; } 
.project .project-container .each-block .left .description-group p { font-size: 20px; line-height: 28px; } 
.project .project-container .each-block .left .info-group { display: flex; align-items: center; } 
.project .project-container .each-block .left .info-group .company { padding: 16px 24px; width: 264px; border-right: 0.5px solid white; } 
.project .project-container .each-block .left .info-group .company .title { font-size: 16px; font-family: 'Russo One'; margin-top: 0; margin-bottom: 8px; line-height: 16px; } 
.project .project-container .each-block .left .info-group .contribution { padding: 16px 24px; width: calc(100% - 264px); } 
.project .project-container .each-block .left .info-group .contribution .title { font-size: 16px; font-family: 'Russo One'; margin-top: 0; margin-bottom: 8px; line-height: 16px; } 
.project .project-container .each-block .right { width: 437px; display: flex; align-items: center; justify-content: center; } 

/* 홈 공지 및 저작권 */
.notice { padding: 100px 0 72px; text-align: center; } 
.notice p:first-child { font-size: 24px; line-height: 42px; margin-bottom: 32px; } 
.notice p:first-child span { color: #B14BFF; font-weight: 700; cursor: pointer; } 
.notice p:first-child span:hover { color: #9B17FF; } 
.notice p:nth-child(2) { font-size: 28px; font-weight: 700; margin-bottom: 40px } 
.notice .to-top { font-size: 14px; cursor: pointer; text-decoration: underline; } 
.notice .to-top:hover { color: #B7B7B7; } 
.notice .copyright { margin-top: 80px; font-size: 14px; } 

/* Vault */
.motto { margin-top: 70px; font-size: 24px; font-weight: 700; } 
.motto-eng { margin-top: 4px; font-size: 20px; } 
.dashboard-container { width: 1000px; margin-top: 60px; display: flex; align-items: center; justify-content: space-between; } 
.dashboard-container .each-box-cover { display: flex; align-items: center; justify-content: center; width: 180px; height: 180px; padding: 5px; border: 0.5px solid white; border-radius: 50%; box-shadow: 0 4px 12px 0 rgba(255, 255, 255, 0.25); } 
.dashboard-container .each-box-cover .each-box { display: flex; align-items: center; justify-content: center; flex-direction: column; width: 100%; height: 100%; background-color: rgba(52, 52, 52, 0.50); border-radius: 50%; } 
.dashboard-container .each-box-cover .each-box:hover { background: linear-gradient(135deg, #180C96 0%, #D21010 0%, #A90EAB 0.01%, rgba(24, 12, 150, 0.99) 0.02%, rgba(74, 11, 157, 0.76) 29.17%, rgba(169, 14, 171, 0.55) 56.25%, rgba(180, 117, 172, 0.26) 83.33%, rgba(255, 255, 255, 0.00) 100%); } 
.dashboard-container .each-box-cover .each-box .title { font-size: 18px; font-weight: 700; margin-bottom: 20px; } 
.dashboard-container .each-box-cover .each-box .year { font-size: 24px; font-weight: 700; } 
.content-container { width: 1200px; margin-top: 80px; border-top: 0.5px solid #B7B7B7; } 
.content-container .each-block .top-line { display: flex; align-items: center; justify-content: space-between; cursor: pointer; height: 60px; border-bottom: 0.5px solid #B7B7B7; } 
.content-container .each-block .top-line .title { font-family: 'Russo One'; font-size: 24px; opacity: 0.6 } 
.content-container .each-block .top-line:hover .title { color: #B7B7B7; } 
.content-container .each-block .content { padding: 10px 20px 20px 20px; } 
.content-container .each-block.active .top-line { border-bottom: 0; } 
.content-container .each-block.active .top-line .title { opacity: 1; } 
.content-container .each-block.active .content { border-bottom: 0.5px solid #B7B7B7; animation: open-animation 300ms ease; } 
.vault-copyright { margin-top: 80px; margin-bottom: 72px; font-size: 14px; } 

@keyframes open-animation { 
 0% { transform: translateY(-30px); } 

 100% { transform: translateY(0); } 
 }